.current h1 {
  font-size: 3.5rem;
  font-weight: 300 !important; }

.current img {
  display: inline-block;
  height: 3.5rem;
  width: auto;
  padding: 0;
  margin: 0 .2rem 0 0;
  position: relative;
  top: .6rem; }

.current sup {
  font-size: .9rem;
  margin: 0;
  padding: 0;
  position: relative;
  top: -2rem;
  color: #848484; }
  .current sup a {
    margin: 0 .5rem 0; }
    .current sup a:first-child {
      margin-left: 0; }
    .current sup a:last-child {
      margin-right: 0; }
    .current sup a, .current sup a:hover, .current sup a:focus {
      color: inherit;
      cursor: pointer; }
    .current sup a.active {
      color: #212529; }



